<template>
    <div class="slide">
        <p class="name-day">{{dayNum}}</p>
        <p class="time">{{timeDay}}</p>
        <img v-if="img" :src="require(`../../public/final_line/${img}.svg`)" alt="" class="weather-icon">
        <p class="weather-temp">{{ Math.round(temp) + '°'}}</p>
    </div>
</template>

<script setup>
    import { computed } from 'vue';
    import { getTimeDay, getTime } from '../utils/index.js';

    const props = defineProps({
        temp: Number,
        img: String,
        day: Number,
        timeZone: Number,
    });

    const time = computed(() => {
        return getTime(props.day + props.timeZone);
    })
    function getDaysWeek(time) {
        const days = [
        'Sun',
        'Mon',
        'Tue',
        'Wed',
        'Thu',
        'Fri',
        'Sat'
        ];
        const d = new Date(time);
        const n = d.getDay();
        // console.log(d); 
        return days[n];       
    }    

    const dayNum = computed(() => {
       return getDaysWeek(time.value);
    })

    const timeDay = computed(() => {
       return getTimeDay(time.value);
    })

    // console.log(props.dt);
</script>

