import { createApp } from 'vue';
import VueProgressBar from "@aacassandra/vue3-progressbar";
import App from './App.vue';

const options = {
  color: "#274e13",
  failedColor: "#ff0000",
  thickness: "2px",
  transition: {
    speed: "0.4s",
    opacity: "0.6s",
    termination: 500,
  },
  autoRevert: true,
  location: "top",
  inverse: false,
  position: "fixed",
};

const app = createApp(App);

app
    .use(VueProgressBar, options)
    .mount('#app')
