<template>
    <div class="weather-info-dop">
                <div class="weather-wind">
                    <div class="wind-info">
                        <p class="wind-direct">{{cardinalWind}}</p>
                        <p class="wind-speed">{{ speed + ' m/sec'}}</p>
                    </div>
                    <div class="wind-logo">
                        <img src="../../public/Компас.png" alt="empty">
                        <svg viewBox="0 0 24 24" fill="none" xmlns="http://www.w3.org/2000/svg"><path d="M12.0321 1.01712L7.75751 5.22761L9.161 6.65246L11.0197 4.82165L10.9644 22.9768L12.9644 22.9829L13.0195 4.86974L14.8177 6.69525L16.2425 5.29175L12.0321 1.01712Z" fill="currentColor" /></svg>
                    </div>
                </div>
                <div class="weather-sun">
                    <img src="../../public/earth.svg" alt="empty" class="earth">
                    <!-- <img src="./img/sun-direct.svg" alt="" class="sun-direct"> -->
                    <div class="sunrise-container">
                        <img src="../../public/final_line/sunrise.svg" alt="" class="sunrise">
                        <p class="sunrise-time">{{ sunRiseTime }}</p>
                    </div>
                    <div class="sunset-container">
                        <img src="../../public/final_line/sunset.svg" alt="" class="sunset">
                        <p class="sunset-time">{{ sunSetTime }}</p>
                    </div>
                    <!-- <div class="sun-anim"><img src="./img/final_line/clear-day.svg" alt=""></div> -->
                    <div class="moon-anim"><img src="../../public/moon-svgrepo-com.svg" alt="empty"></div>
                    <div class="sun-day-duration">
                        <p class="text-dur">Световой день:</p>
                        <b class="val-dur">{{ dayDuration }}</b>
                    </div>
                </div>
            </div>
</template>

<script setup>
    import { computed } from 'vue';
    import { getTimeDay, getTime } from '../utils/index.js';

    const props = defineProps({
        speed: Number,
        direction: Number,
        sunrise: Number,
        sunset: Number,
        timeZone: Number,
    });

    const timeSunRise = computed(() => {
        return getTime(props.sunrise + props.timeZone);
    })    
    const timeSunSet = computed(() => {
        return getTime(props.sunset + props.timeZone);
    })    
    const durationDay = computed(() => {
        return getTime(props.sunset - props.sunrise);
    })

    const sunRiseTime = computed(() => {
        return getTimeDay(timeSunRise.value);
    })

    const sunSetTime = computed(() => {
        return getTimeDay(timeSunSet.value);
    })

    const dayDuration = computed(() => {
        return getTimeDay(durationDay.value);
    })

    const cardinalWindString = (degree) => {
        if (degree > 335 && degree < 360 || degree > 0 && degree < 20) {
            document.querySelector('.wind-logo svg').style.transform = `translateY(-50%) translateX(-50%) rotate(${degree - 180}deg)`;
            return 'North';
        } else if (degree > 65 && degree < 115) {
            document.querySelector('.wind-logo svg').style.transform = `translateY(-50%) translateX(-50%) rotate(${degree - 180}deg)`;
            return 'East';
        } else if (degree > 155 && degree < 205) {
            document.querySelector('.wind-logo svg').style.transform = `translateY(-50%) translateX(-50%) rotate(${degree - 180}deg)`;
            return 'South';
        } else if (degree > 245 && degree < 295) {
            document.querySelector('.wind-logo svg').style.transform = `translateY(-50%) translateX(-50%) rotate(${degree - 180}deg)`;
            return 'West';
        } else if (degree >= 20 && degree <= 65) {
            document.querySelector('.wind-logo svg').style.transform = `translateY(-50%) translateX(-50%) rotate(${degree - 180}deg)`;
            return 'NorthEast';
        } else if (degree >= 115 && degree <= 155) {
            document.querySelector('.wind-logo svg').style.transform = `translateY(-50%) translateX(-50%) rotate(${degree - 180}deg)`;
            return 'SouthEast';
        } else if (degree >= 205 && degree <= 245) {
            document.querySelector('.wind-logo svg').style.transform = `translateY(-50%) translateX(-50%) rotate(${degree - 180}deg)`;
            return 'SouthWest';
        } else if (degree >= 295 && degree <= 335) {
            return 'NorthWest';
        }
    }

    const cardinalWind = computed(() => {
        return cardinalWindString(props.direction);
    })


</script>

<style scoped>
.weather-info-dop{
    display: grid;
    grid-template-rows: 1fr 2fr;
    gap: .5rem;
}

.weather-sun,
.weather-wind
{
    border-radius: 10px;
    backdrop-filter: blur(3px);
    border: 2px solid rgba(255,255,255,0.2);
}

/*Ветер*/
.weather-wind{
    padding: calc(var(--index)*.4);
    display: flex;
    justify-content: center;
    align-items: center;
}

.wind-info {
    margin-right: calc(var(--index)*4);
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 5px;
}

.wind-info p{
    font-size: calc(var(--index)*.8);
}

.wind-logo {
    position: relative;
}

.wind-logo img{
    height: calc(var(--index)*8);
    width: calc(var(--index)*8);
}

.wind-logo svg {
    height: calc(var(--index)*3);
    width: calc(var(--index)*3);
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translateY(-50%) translateX(-50%) rotateZ(175deg);
    color: #ffffff8a;
}
/*Ветер конец*/

/*Восход закат*/
.weather-sun {
    position: relative;
    display: flex;
    justify-content: space-between;
}

.earth {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translateX(-50%) translateY(-50%);
}

.sunrise-container, .sunset-container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.sunrise, .sunset {
    width: calc(var(--index)*3.5);
}

.sunset-time, .sunrise-time {
    font-size: calc(var(--index)*.7);
    color: #ffffff8a;
}

.sun-anim, .moon-anim {
    width: 80%;
    position: absolute;
    top: 50%;
    left: 10%;
    transform-origin: 50% 0;
    /* transform: rotateZ(50deg); */

    animation-duration: 6s;  
    animation-timing-function: linear;
    animation-direction: reverse;
    animation-iteration-count: infinite; 

}

/* .sun-anim img {
    width: calc(var(--index)*3.5);
    transform: translateY(-50%);
} */

.moon-anim img {
    width: calc(var(--index)*2.5);
    /* transform: translateY(-50%); */
}

.sun-day-duration {
    position: absolute;
    top: 2%;
    left: 50%;
    transform: translateX(-50%);
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    gap: .5rem;
    font-size: calc(var(--index)*.7);
}
/* .sun-anim, .moon-anim {
    animation-name: sun-dir;
}

@keyframes sun-dir {
    from { 
        transform: rotate(0.06turn);
    }
    45% { 
        transform: rotate(.45turn);
        opacity: 1;
    }
    50% { 
        transform: rotate(.5turn);
        opacity: 0;
    }
    to {
        transform: rotate(1turn);
        opacity: 0;
        color: #ec780c88;
    }
  } */


.moon-anim {
    animation-name: moon-dir;
}

@keyframes moon-dir {
    from { 
        transform: rotate(40deg);
    }
    50% {
        transform: rotate(44deg);
    }
    to {
        transform: rotate(40deg);
    }
  }
/* .sun-direct {
    width: 80%;
    height: 34%;
    position: absolute;
    left: 10%;
    top: 50%;
    transform: translateY(-70%);
} */
/*Восход закат конец*/

@media (max-width: 1100px) {
    .header>input {
        height: 70%;
    }
    h2 {
        font-size: calc(var(--index)*2);
    }
    .weather-info>p {
        font-size: calc(var(--index)*2.5);    
        display: block;
    }
    
    .description, .min-max {
        font-size: calc(var(--index)*1.2);    
    }
    .main-container {
        grid-template-rows: 1fr 3fr;
    }
    .container-bottom {
        grid-template-columns: 1fr;
        grid-template-rows: 1fr 1fr;
    }
    .slide p {
        font-size: calc(var(--index)*1.1);
    }
    .weather-info-items {
        grid-template-columns: 2fr 1.5fr;
    }
    .weather-wind{
        justify-content: space-around;
    }
    .wind-info p{
        font-size: calc(var(--index)*1.1);
    }
    .wind-info {
        margin-right: 0;
    }
    .wind-logo img{
        height: calc(var(--index)*10);
        width: calc(var(--index)*10);
    }
    
    .wind-logo svg {
        height: calc(var(--index)*3);
        width: calc(var(--index)*3);
    }

    .day-num, .min-temp, .max-temp {
        font-size: calc(var(--index)*1.1);
    }
    .container-content p{
        font-size: calc(var(--index)*1.1);
    }

    .earth {
        height: calc(var(--index)*15);
    }
    .sunrise, .sunset {
        width: calc(var(--index)*6.5);
    }
    
    .sunset-time, .sunrise-time {
        font-size: calc(var(--index)*1.1);
        color: #ffffff8a;
    }
    .sun-anim, .moon-anim {
        position: absolute;
        top: 50%;
        transform-origin: 50% 0;
    
        /* animation-duration: 60s;  
        animation-timing-function: linear;
        animation-iteration-count: infinite;  */
    
    }
    
    /* .sun-anim img {
        width: calc(var(--index)*4.5);
        transform: translateY(-50%);
    } */
    
    .moon-anim img {
        width: calc(var(--index)*3.5);
        transform: translateY(-50%);
    }
    
    .sun-day-duration {
        font-size: calc(var(--index)*1.1);
    }
}
</style>