<template>
    <div class="weather-current">
        <div class="carousel">
            <WeatherCurrent
            v-if="weatherDays"
            v-for="weatherDay in filterWeatherDays"
            :key="weatherDay.dt"
            :temp="weatherDay.main.temp"    
            :img="weatherDay.weather[0].icon"
            :day="weatherDay.dt"  
            :timeZone ="timeZone"                  
            />
        </div>
    </div>
</template>

<script setup>
import { computed } from 'vue';
import WeatherCurrent from './WeatherCurrent.vue'

const props = defineProps({
  weatherDays:Array,
  timeZone: Number,
})

const filterWeatherDays = computed(() => {
    // console.log(props.weatherDays?.filter(day => day.dt_txt.includes('09:00:00')))
    return props.weatherDays?.slice(0,12);
})

// console.log(props.weatherDays?.value);
