<template>
    <div class = "blur"></div>
    <div class="main-container">
        <div class="container-top">
            <div class="header">
                <svg width="24" height="24" viewBox="0 0 24 24" fill="none" xmlns="http://www.w3.org/2000/svg"><path fill-rule="evenodd" clip-rule="evenodd" d="M18.319 14.4326C20.7628 11.2941 20.542 6.75347 17.6569 3.86829C14.5327 0.744098 9.46734 0.744098 6.34315 3.86829C3.21895 6.99249 3.21895 12.0578 6.34315 15.182C9.22833 18.0672 13.769 18.2879 16.9075 15.8442C16.921 15.8595 16.9351 15.8745 16.9497 15.8891L21.1924 20.1317C21.5829 20.5223 22.2161 20.5223 22.6066 20.1317C22.9971 19.7412 22.9971 19.1081 22.6066 18.7175L18.364 14.4749C18.3493 14.4603 18.3343 14.4462 18.319 14.4326ZM16.2426 5.28251C18.5858 7.62565 18.5858 11.4246 16.2426 13.7678C13.8995 16.1109 10.1005 16.1109 7.75736 13.7678C5.41421 11.4246 5.41421 7.62565 7.75736 5.28251C10.1005 2.93936 13.8995 2.93936 16.2426 5.28251Z" fill="currentColor" /></svg>
                <input type="text" name="city-name" placeholder="City name" title="" required @keyup.enter = 'inputCity'>
            </div>
            <HeaderContent 
            :name = "items?.name" 
            :img = "items?.weather[0].icon" 
            :temp = "items?.main.temp" 
            :description = "items?.weather[0].description"
            :temp_min = "items?.main.temp_min"
            :temp_max = "items?.main.temp_max"
            />
        </div>
        <div class="container-bottom">
            <div class="weather-info-items">
                <WeatherCurrentList :weatherDays="weatherDays" :timeZone="items?.timezone"/>
                <WeatherDailyList :weatherDays="weatherDays" :timeZone="items?.timezone"/>
                <WeatherDopInfoList :items="items" :timeZone="items?.timezone"/>
            </div>
            <WeatherWindSunInfo :speed="items?.wind.speed" :direction="items?.wind.deg" :sunrise="items?.sys.sunrise" :sunset="items?.sys.sunset" :timeZone="items?.timezone"/>
        </div>
    </div>
    <div class="error-block">
      <div class="error-content">
        <p>City not found!</p>
        <img src="../public/sad.png" alt="">
      </div>
      <div></div>
    </div>
    <vue-progress-bar></vue-progress-bar>
</template>

<script setup>
import { ref, onMounted, computed} from 'vue';
import axios from 'axios';
import { getCurrentInstance } from "vue"
import HeaderContent from './components/HeaderContent.vue';
import WeatherCurrentList from './components/WeatherCurrentList.vue';
import WeatherDailyList from './components/WeatherDailyList.vue';
import WeatherDopInfoList from './components/WeatherDopInfoList.vue';
import WeatherWindSunInfo from './components/WeatherWindSunInfo.vue';

const internalInstance = getCurrentInstance()// переменная для получения глобальных переменных типа this. ЛУЧШЕ не использовать!(https://v3.ru.vuejs.org/ru/api/composition-api.html#getcurrentinstance)
const items = ref(null);
const city = ref();
const curentPosition = ref();
const weatherDays = ref([]);
const weatherBg = ref({
  '01n':'night_sky',
  '01d':'sun_4',
  '02n':'few_clouds_night',
  '02d':'few_clouds_1',
  '03n':'cloud_night',
  '03d':'cloud',
  '04n':'cloud_night',
  '04d':'cloud',
  '09n':'rain',
  '09d':'rain',
  '10n':'rain',
  '10d':'rain',
  '11n':'thunderstorm',
  '11d':'thunderstorm',
  '13n':'snow',
  '13d':'snow',
  '50n':'mist_3',
  '50d':'mist_3',
});
const flagError = ref(false);


async function inputCity(event){
  city.value = event.target.value;
  await fetchWeatherCityName();
  await fetchWeatherCityNameDaysValue();
  document.querySelector('body').style.backgroundImage = `url(${require(`../public/background/${weatherBg.value[items?.value.weather[0].icon]}.jpg`)})`;
  flagError.value ? document.querySelector('.error-block').style.display = 'flex' : document.querySelector('.error-block').style.
  display = 'none';
  setTimeout(() => {
    document.querySelector('.error-block').style.display = 'none';
    flagError.value = false;
  }, 3000);
  event.target.value = '';
}

const fetchWeatherCityName = async () => {
  try{
    internalInstance.appContext.config.globalProperties.$Progress.start()
    const {data} = await axios.get(`https://api.openweathermap.org/data/2.5/weather?q=${city.value}&units=metric&appid=0716837b9a75dd887ca21199c6773ad0`);
    items.value = data;
    
    internalInstance.appContext.config.globalProperties.$Progress.finish()
  }catch(err) {
    console.log(err)
    flagError.value = true;
    internalInstance.appContext.config.globalProperties.$Progress.fail()
  }
}

const fetchWeatherCoordsValue = async (lat,lon) => {
  try{
    const {data} = await axios.get(`https://api.openweathermap.org/data/2.5/weather?lat=${lat}&lon=${lon}&units=metric&appid=0716837b9a75dd887ca21199c6773ad0`);

    items.value = data;
    
  }catch(err) {
    flagError.value = true;
    console.log(err)
  }
}

const fetchWeatherCoordsDaysValue = async (lat,lon) => {
  try{
    const {data} = await axios.get(`https://api.openweathermap.org/data/2.5/forecast?lat=${lat}&lon=${lon}&units=metric&appid=0716837b9a75dd887ca21199c6773ad0`);

    weatherDays.value = data.list;
    
  }catch(err) {
    flagError.value = true;
    console.log(err)
  }
}

const fetchWeatherCityNameDaysValue = async () => {
  try{
    const {data} = await axios.get(`https://api.openweathermap.org/data/2.5/forecast?q=${city.value}&units=metric&appid=9271c6ed58d362b08ffb71ff8143edb1`);
    weatherDays.value = data.list;
    
  }catch(err) {
    flagError.value = true;
    console.log(err)
  }
}

/*Функции для локации */

const getCurentCoords = async() => {
  
  function getLongAndLat() {
    return new Promise((resolve, reject) =>
        navigator.geolocation.getCurrentPosition(resolve, reject)
    );
  }

  try{
    let position = await getLongAndLat();
    // console.log(position);
    let { coords } = position;
    curentPosition.value = [coords.longitude, coords.latitude];
  } catch(err) {
    curentPosition.value = [37.4634945, 55.7980647];
  }
  
}
/*Функции для локации конец */


/**
 * Запрос на сервер в момент загрузки App 
 */
onMounted(async () => {
  /*Получение локации */
  await getCurentCoords();
  
  await fetchWeatherCoordsValue(curentPosition.value[1],curentPosition.value[0]);
  await fetchWeatherCoordsDaysValue(curentPosition.value[1],curentPosition.value[0]);
  document.querySelector('body').style.backgroundImage = `url(${require(`../public/background/${weatherBg.value[items?.value.weather[0].icon]}.jpg`)})`;
  // console.log(items)
  // console.log(weatherBg.value['01n'])
  // console.log(items?value.weather[0].icon)

  
  // city.value = items.value.name;
  // console.log(city.value);
  /*Получение локации конец */
 
})



</script>

