<template>
    <div class="weather-daily">
        <WeatherDaily
        v-if="weatherDays"
        v-for="weatherDay in newWeatherDays"
        :key="weatherDay.dt"
        :temp_min="weatherDay.main.temp_min" 
        :temp_max="weatherDay.main.temp_max"     
        :img="weatherDay.weather[0].icon"
        :day="weatherDay.dt"  
        :timeZone ="timeZone"        
        />
    </div>
</template>

<script setup>
import { computed } from 'vue';
import WeatherDaily from './WeatherDaily.vue'

const props = defineProps({
  weatherDays:Array,
  timeZone: Number,
})

const newWeatherDays = computed(() => {
    // console.log(props.weatherDays?.filter(day => day.dt_txt.includes('09:00:00')))
    return props.weatherDays?.filter(day => day.dt_txt.includes('09:00:00'))
})

</script>

