<template>
    <div class="daily-info">
        <p class="day-num">{{dayNum}}</p>
        <img v-if="img" :src="require(`../../public/final_line/${img}.svg`)" alt="" class="day-icon">
        <div class="min-max-temp">
            <p class="min-temp">{{ Math.round(temp_min) + '°' }}</p>
            <p class="max-temp">{{ Math.round(temp_max) + '°' }}</p>
        </div>
    </div>
</template>

<script setup>
    import { computed } from 'vue';

   const props = defineProps({
        temp_min: Number,
        temp_max: Number,
        img: String,
        day: Number,
        timeZone: Number,
    });

    function getDaysWeek(unixtime) {
        const days = [
        'Sun',
        'Mon',
        'Tue',
        'Wed',
        'Thu',
        'Fri',
        'Sat'
        ];
        const d = new Date(unixtime*1000);
        const n = d.getDay();
        // console.log(d); 
        return days[n];       
    }

    function getTimeDay(unixtime) {

        const date = new Date(unixtime*1000);
        const hours = date.getHours();

        // Minutes part from the timestamp
        const minutes = "0" + date.getMinutes();

        // Seconds part from the timestamp
        const seconds = "0" + date.getSeconds();

        // Will display time in 10:30:23 format
        const formattedTime = hours + ':' + minutes.substr(-2);
        return formattedTime;       
    }

    const dayNum = computed(() => {
       return getDaysWeek(props.day - props.timeZone);
    })
</script>

